<!--
Axios 설치하기:
1) npm install axios

Mitt library 설치하기: (멀리 있는 컴포넌트간 데이터전송하기)
1) npm install mitt
2) main.js 수정

Vuex 설치하기
1) npm install vuex@next --save
2) store.js
3) main.js:
import store from './store.js'
app.use(store).mount('#app')
-->

<template>
  <div class="header">
      <ul class="header-button-left">
        <li v-if="step==1 | step==2" @click="step--">Cancel</li>
      </ul>
      <ul class="header-button-right">
        <li v-if="step==1" @click="step++">Next</li>
        <li v-if="step==2" @click="step==0; publish($store)">Publish</li>
      </ul>
      <img src="./assets/logo.png" class="logo" />
    </div>

    <!-- component inside App.vue -->
    <ContainerC :게시물="게시물" :step="step" :이미지="이미지" @write="작성한글=$event" @writeName="내이름=$event" />

    <!-- 더보기 버튼 : show one more post, generated from URL inside more() -->
    <!-- Способ 1 -->
    <!-- ajax 요청 -->
    <button v-if="step==0 && this.newPost<2" @click="more($store)" class="buttonPlus">더보기</button>

    <!-- 여기부터 test -->
    <div v-if="testing==true"> 
      <!-- Способ 2 -->
      <!-- vuex(store.js) > actions 함수 사용 (also ajax 요청) -->
      <!-- click button -> getData() gets URL data and puts it into more -->
      <!-- <button v-if="step==0 && this.newPost<2" @click="$store.dispatch('getData')" class="buttonPlus">더보기버튼</button> -->
      <button v-if="step==0 && this.newPost<2" @click="$store.dispatch('getData')" class="buttonPlus">Vuex 더보기</button>
      <!-- next is rewritting such that 게시물 was stored in store.js -->
      <!-- show 'more' from store.js -->
      <p>more = {{ $store.state.more }}</p>

      <!-- to compare 'methods' and 'computed' -->
      <button @click="count++" class="buttonPlus">Test now</button>
      <p>{{ count }}: {{ now1() }}</p>  <!-- 'methods': click button -> rendering now1() -->
      <p>{{ count }}: {{ now2 }}</p>  <!-- 'computed': click button -> now2() returns the same -->

      <!-- computed func: $store.state.name 짧게 쓰기 -->
      <p>{{ name }}</p>

      <!-- computed func: mapState 사용 -->
      <p>{{ name }} {{ age }} {{ likes }}</p>
      <p>{{ 작명 }} {{ 작명2 }} {{ 작명3 }}</p>
      <button @click="testMutat()">버튼</button>

    </div>
    <!-- 여기까지 test -->

    <div class="footer">
      <ul class="footer-button-plus">
        <input type="file" accept="image/*" @change="upload" @click="작성한글=''" id="file" class="inputfile" />
        <label for="file" class="input-plus">+</label>
      </ul>
  </div>

  <div v-if="showTab == true">
  <!-- 탭, move to another page when click, send step to ContainerC -->
  <button @click="step=0" class="buttonTab">Feed</button>
  <button @click="step=1" class="buttonTab">Filters</button>
  <button @click="step=2" class="buttonTab">Description</button>
  </div>

</template>

<script>
import ContainerC from './components/ContainerC.vue';
import postdata from './postdata.js';  // initial post data
import axios from 'axios';  // axios for post/get 요청 from URL
// axios.post();
import {mapMutations, mapState} from 'vuex';

export default {
  name: 'App',
  data() {
    return {
      게시물: postdata,
      newPost: 0,
      step: 0,
      이미지: '',
      작성한글: '',
      발행필터: '',
      내이름: '',
      testing: false,
      count: 0,
      showTab: false,
    }
  },
  components: {
    ContainerC: ContainerC,
  },
  methods: {
    //1
    //using ajax to get data for 1 more post after clicking 더보기 button
    //'push' to the end of 게시물 array
    more(store) {
      axios.get(`https://codingapple1.github.io/vue/more${this.newPost}.json`)
      .then((content)=>{
        console.log(content.data);
        this.게시물.push(content.data);
        this.newPost++;
      });
      //generate new likes quantity and 'not liked'
      store.state.likes.push(
        store.state.likes[store.state.likes.length-1] - (store.state.likes[store.state.likes.length-1]%10) + 10);
        //e.g.  13 - (13 % 10) + 10 = 20
      store.state.liked.push(0);
    },
    //2
    //upload file from comp to website
    //create img url and save it
    //change step to move img url to another page
    upload(e) {
      let 파일 = e.target.files;
      console.log(파일[0]);
      let url = URL.createObjectURL(파일[0]);
      console.log(url); 
      this.이미지 = url;
      this.step = 1;
    },
    //3
    //add new post with uploaded img and written description
    //'unshift' to the beginning of 게시물 array
    publish(store) {
      var myPost = {
        name: this.내이름,
        userImage: "https://picsum.photos/100?random=4",
        postImage: this.이미지,
        likes: 100,
        date: "July 25",
        liked: false,
        content: this.작성한글,
        filter: this.발행필터,
      };
      this.게시물.unshift(myPost);
      this.step = 0;
      //add 0 likes and 'not liked' since it's a new post
      store.state.likes.unshift(0);
      store.state.liked.unshift(0);
    },
    //test func. to compare 'methods' and 'computed'
    //current time
    now1() {
      return new Date();
    },
    //test func. vuex mutations 함수 한번에 꺼내 쓰기
    //don't need to use $store.commit()
    //need import
    ...mapMutations(['testMutat', ]),
  },

  computed: {
    //test func. to compare 'methods' and 'computed'
    now2() {
      return new Date();
    },
    //test func. <template> 안에서 $store.state.name 짧게 쓰기
    name() {
      return this.$store.state.name;
    },
    //test func. vuex state 한번에 꺼내쓰기
    //need import
    ...mapState(['name', 'age', 'likes']),
    ...mapState({ 작명: 'name', 작명2: 'age', 작명3: 'likes'}),  //object type
  },

  //using mitt library we sent 해당 필터명 FilterC->App to apply it to myPost
  mounted() {
    this.emitter.on('작명', (필터명)=>{
      console.log(필터명);
      this.발행필터 = 필터명;
    })
  }

}
</script>

