<template>
    <div v-if="step==0">
        <h2 class="textCenter">Vuestagram Feed</h2>
        <PostC v-for="(p, postInd) in 게시물" :key="postInd" :게시물="게시물[postInd]" :postInd="postInd" />
    </div>

    <!-- 필터선택페이지 -->
    <div v-if="step==1">
        <h2 class="textCenter">Choose Filter</h2>
        <div class="upload-image" :class="선택필터" :style="`background-image: url(${이미지})`"></div>
        <div class="filters">
            <FilterC v-for="(f,i) in 필터들" :key="i" :이미지="이미지" :필터="필터들[i]">
                {{ f.toUpperCase() }}
            </FilterC>
        </div>
    </div>

    <!-- 글작성페이지 -->
    <div v-if="step==2">
        <h2 class="textCenter">Add Description</h2>
        <div class="upload-image" :class="선택필터" :style="`background-image: url(${이미지})`"></div>
        <div class="write">
            <!-- 하위->상위 데이터 보냄 : 하위에서 custom event ($emit) and 상위에서 @write... -->
            <textarea class="write-box" @input="$emit('write', $event.target.value)" placeholder="Write!"></textarea>
        </div>
        <form>
            <label for="fname" class="myname">User name: </label><br>
            <input type="text" id="namebox" @input="$emit('writeName', $event.target.value)">
        </form>
    </div>


</template>

<script>
import PostC from './PostC.vue';
import FilterC from './FilterC.vue';

export default {
    name: 'ContainerC',
    data() {
        return {
            필터들: [ "original", "aden", "_1977", "brannan", "brooklyn", "clarendon", "earlybird", "gingham", "hudson", 
                    "inkwell", "kelvin", "lark", "lofi", "maven", "mayfair", "moon", "nashville", "perpetua", 
                    "reyes", "rise", "slumber", "stinson", "toaster", "valencia", "walden", "willow", "xpro2"],
            선택필터: '',
        }
    },
    components: {
        PostC: PostC,
        FilterC: FilterC,
    },
    props: {
        게시물: Array,
        step: Number,
        이미지: String,
    },
    mounted() {
        this.emitter.on('작명', (필터명)=>{
            this.선택필터 = 필터명;
        })
    }
}
</script>

<style>
.upload-image{
    width: 100%;
    height: 450px;
    background: cornflowerblue;
    background-size : cover;
}
.filters{
    overflow-x:scroll;
    white-space: nowrap;
}
.filter-1 {
    width: 100px;
    height: 100px;
    background-color: cornflowerblue;
    margin: 10px 10px 10px auto;
    padding: 8px;
    display: inline-block;
    color : white;
    background-size: cover;
}
.filters::-webkit-scrollbar {
    height: 5px;
}
.filters::-webkit-scrollbar-track {
    background: #f1f1f1; 
}
.filters::-webkit-scrollbar-thumb {
    background: #888; 
    border-radius: 5px;
}
.filters::-webkit-scrollbar-thumb:hover {
    background: #555; 
}
.write-box {
    border: none;
    width: 90%;
    height: 100px;
    padding: 15px;
    margin: auto;
    display: block;
    outline: none;
}

/* my */

.myname {
    margin-left: 10px;
    color: rgb(133, 133, 133);
    font-size: 14px;
}
#namebox {
    margin: 5px 0 20px 10px;
    width: 30%;
    height: 100%;
    font-size: 14px;
    color: rgb(81, 81, 81);
    border: 1.2px solid rgb(217, 217, 217);
    border-radius: 4px;
}

</style>