<template>
    <div @click="fire" class="filter-item" :class="`${필터}`" :style="`background-image: url(${이미지})`" >
        <!-- OR :class="필터 + ' filter-item'"
                :class="필터" class="filter-item" -->
        <!-- 1st way to show 필터명 -->
        <!-- 2nd way is slot -->
        <slot></slot>
    </div>
</template>

<script>
export default {
    name: 'FilterC',
    data() {
        return {
        }
    },
    props: {
        이미지: String,
        필터: String,
    },
    methods: {
        //1
        //use mitt library
        //1) to send 해당 필터명 FilterC->App to unshift to 게시물
        //2) to send 해당 필터명 FilterC->ContainerC to apply it to 필터선택페이지 & 글작성페이지
        fire() {
            this.emitter.emit('작명', this.필터);
        }
    }
}
</script>

<style>
.filter-item {
  width: 100px;
  height: 100px;
  margin: 10px 10px 10px auto;
  padding: 8px;
  display: inline-block;
  color : white;
  background-size: cover;
  background-position : center;
}
</style>