// 모든 컴퍼먼트가 공유하는 공간
// For using Vuex

import { createStore } from "vuex";
import axios from 'axios';  // to use axios func

const store = createStore({
    state () {
        return {
            // 모든 컴퍼먼트가 공유하는 데이터: 쓰기, 변경 가능
            name: 'Anna',  //test
            age: 20,  //test
            likes: [10, 20, 30],
            liked: [0, 0, 0, 0],
            more: {},  // 더보기 버튼으로 ajax 요청 > getData() > data for new post from URL
            newPost: 0,
        }
    },
    // functions to change state(값) of data
    // can be used by any .vue using $store.commit('func', data)
    mutations: {
        좋아요누름(state, i) {
            state.liked[i] = 1;
            state.likes[i]++;
        },
        좋아요치움(state, i) {
            state.liked[i] = 0;
            state.likes[i]--;
        },
        //test func. for state 변경 after getting URL data in getData()
        addMore(state, data) {
            state.more = data;
        },
        // test func. for '한번에 꺼내 쓰기' (look in App.vue)
        testMutat(state) {
            console.log('성공');
            state.age++;
        }
    },
    actions: {
        // ajax 요청 (더보기 버튼)
        // can write in mutations, but 함수 2개, 함수1은 3초 길럴 수 있음...
        // need to import axios
        //test func.
        // 1. getData(): get URL data by ajax 요청,  2. addMore(): put it into 'more',  3. Add.vue: insert into 게시물
        getData(context) {
            axios.get(`https://codingapple1.github.io/vue/more${this.state.newPost}.json`)
            .then((post)=>{
                //console.log(post.data);
                this.state.newPost++;
                context.commit('addMore', post.data);  // mutations 함수 동작
            })
        },
    },

})

export default store;